@import url(https://fonts.googleapis.com/css2?family=Bungee+Outline&family=Comfortaa:wght@700&family=Concert+One&family=Fredoka+One&family=Old+Standard+TT&family=Source+Sans+Pro:wght@700&display=swap);
*{
    padding: 0px;
    margin:0px 0px 0px 0px;
}
.web-navbar{
    width: 100%;
    height: 80px;

}
.left-navbar{
    width:20%;
    float: left;
}
span{
    color:#14bef0;
    padding: 5px;
    text-align: center;
    width: 30px;
    font-size: 50px;
    margin-left: -15px;
    font-family: 'Bungee Outline', cursive;
}
   .left-navbar p{
        color:#14bef0;
        font-size: 34px;
        font-family: 'Comfortaa', cursive;
        float: left;
        height: 86px;
        overflow: hidden;
        margin-left: 20px;
        margin-top: -30px;
        cursor: pointer;
        
    }

.right-navbar{
    width: 60%;  
    float: left;
}
.doc-div{
    width: 20%;
    float: left;
    height: 51px;
    margin-top: 25px;
    cursor: pointer;
    
}
.doc-div:hover{
    border-bottom:solid #14bef0 4px;
}
.doc-div h1{
    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    color: #1d1d1d;
    font-family: Camphor,Open Sans,sans-serif;letter-spacing: -.3px;
}
.doc-div p{
    font-family: 'Comfortaa', cursive;
    font-size: 10px;
    line-height: 18px;
    color: #787887;
    letter-spacing: -.1px;
}

.login{
    width: 20%;
    float: left;
    text-align: center;
}
.login p{
    font-size: 13px;
    line-height: 13px;
    color: #787887!important;
    border-radius: 4px;
    border: 1px solid #d3d3d3;
    padding: 12px;
    width: 120px;
    margin-top: 25px;
    font-family: 'Comfortaa', cursive;
}
.login p:hover{
    cursor: pointer;
    color:#14bef0;
    border: solid #14bef0 1px;
}
.main-con{
    width: 100%;
    height: 350px;
    background-color: #f8e9e6;
}
.main-con h1{
    color: #2d2d32;
    font-family: Camphor,sans-serif;
    font-weight: 600;
    font-size: 28px;
    padding: 35px 26px 0;
}
.main-con p{
    font-family: Camphor,sans-serif;
    margin-left: 30px;
    margin-top: 35px;
    font-size: 18px;
}
.main-con button{
    background-color: #14bef0;
    padding: 15px;
    cursor: pointer;
    width: 180px;
    border:none;
    color: white;
    font-size: 16px;
    margin-left: 30px;
    margin-top: 40px;
    border-radius: 4px;
    font-weight: bold;
}
.main-con button:hover{
    border:solid #14bef0 1px;
    background-color: transparent;
    color: #14bef0;
}
.float-con{
    margin-left: 50px;
    float: left;
    width:50%;
}

.float-right{
    float:left;
    overflow:hidden;
    width: 45%;

}
.float-right img{
    float:right;
    overflow:hidden;
    width: 83.2%;
    margin-right: 50px;

}
